.PlayerPicks .bets-top {
    display: flex;
    justify-content: space-between;
    background-color: #1d1e1f;
    padding: 15px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px, rgba(0, 0, 0, 0.04) 0px 10px 10px;
    margin-top: 20px;
}

.PlayerPicks .bets-top .left {
    color: white;
    font-weight: 500;
    font-size: 14px;
}

.PlayerPicks .bets-top .right {
    color: white;
}

@keyframes pulsateBar {
    0% {
        opacity: 0.5; /* Start with a lower opacity */
    }
    50% {
        opacity: 1; /* Increase opacity */
    }
    100% {
        opacity: 0.5; /* Return to lower opacity */
    }
}

.PlayerPicks .bets-bar {
    width: 100%;
    height: 4px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px, rgba(0, 0, 0, 0.04) 0px 10px 10px;
}

.PlayerPicks .bets-bottom {
    background-color: #1d1e1f;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px, rgba(0, 0, 0, 0.04) 0px 10px 10px;
}

.PlayerPicks .bets-bottom-logo {
    padding: 20px;
}

.PlayerPicks .bets-bottom-logo .logo2 {
    display: flex;
    justify-content: center;
}

.PlayerPicks .bets-bottom-list .testing {
    color: white;
}

.PlayerPicks .bets-bottom-list .testing .list {
    display: flex;
    align-items: center;
}

.PlayerPicks .bets-bottom-list .testing .MuiAvatar-img {
    height: 3vh;
    width: 3vh;
}

.PlayerPicks .bets-bottom-list .testing .MuiListItemText-secondary {
    color: #6c6d6f;
}

.PlayerPicks .bets-bottom-list .testing .MuiSvgIcon-root {
    color: #a5a6a7;
}
